body
  overflow: hidden

.preloader
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background-repeat: no-repeat
  background-position: 50%
  background-color: #080808
  z-index: 100
  opacity: 1
  transition: opacity 2.2s ease
  &.has-been-loaded
    opacity: 0
    z-index: 1
    pointer-events: none
  
.container
  max-width: 600px
  border: 1px solid grey
  margin: 40px
  padding: 20px

  h2
    margin: 0
    margin-bottom: 10px
  
  p
    margin: 0

canvas
  margin: 40px
  position: absolute
  top: 400px
  left: 8px

img
  position: absolute
  right: 0
  top: 50%
  max-height: 80vh
  cursor: pointer
  transform: scale(1) translateY(-50%)
  transition: all .5s ease
  &:hover
    transform: scale(1.02) translateY(-50%)